.App {
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 60px auto 60px auto;
  width: 70%;
}
.logo-container {
  display: flex;
  /*   border:1px solid red; */
  height: 100%;
  align-items: center;
}

ul.results {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
}

ul.results li {
  width: 24.5%;
  min-height: 240px;
  max-height: 300px;
  margin-right: 0.5%;
  margin-bottom: 0.5%;
}

li img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

input[type="text"] {
  padding: 0.5rem 1rem;
  margin-bottom: 60px;
  min-width: 400px;
}

button[type="submit"] {
  padding: 0.5rem 1rem;
  margin-left: -0.1rem;
  margin-top: 2rem;
}

.pagination-container {
  padding: 3rem 0;
}

ul.pagination {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
  height: 10px;
  margin: 1rem auto;
}

ul.pagination li {
  border: 1px solid silver;
  color: silver;
  padding: 4px 6px;
  cursor: pointer;
}

ul.pagination li.selected {
  border: 1px solid silver;
  color: white;
  background: #bd0034;
}

@import url(https://fonts.googleapis.com/css?family=Montserrat);

svg {
  display: block;
  font: 10.5em "Montserrat";
  width: 960px;
  height: 300px;
  margin: 0 auto;
}

.text-copy {
  fill: none;
  stroke: white;
  stroke-dasharray: 6% 29%;
  stroke-width: 5px;
  stroke-dashoffset: 0%;
  animation: stroke-offset 6.5s infinite linear;
}

.text-copy:nth-child(1) {
  stroke: #4d163d;
  animation-delay: -1;
}

.text-copy:nth-child(2) {
  stroke: #840037;
  animation-delay: -2s;
}

.text-copy:nth-child(3) {
  stroke: #bd0034;
  animation-delay: -3s;
}

.text-copy:nth-child(4) {
  stroke: #bd0034;
  animation-delay: -4s;
}

.text-copy:nth-child(5) {
  stroke: #fdb731;
  animation-delay: -5s;
}

@keyframes stroke-offset {
  100% {
    stroke-dashoffset: -35%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 600px) {
  .container {
    margin: 20px auto 20px auto;
    width: 100%;
  }
  form {
    text-align: center;
  }
  ul.results {
    flex-direction: column;
  }

  ul.results li {
    width: 100%;
    min-height: auto;
    max-height: 260px;
    height: 260px;
    margin-right: 0.5%;
    margin-bottom: 0.5rem;
  }
}
